@import url(https://fonts.googleapis.com/css?family=Roboto:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.loginform{
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}


html{
	font-size: 10px;
	font-family: "Roboto Cn", sans-serif;
}

a{
	text-decoration: none;
	color: #eee;
}

header{
	width: 100%;
	height: 100vh;

	background-size: cover;
	position: relative;
  overflow: hidden;
}

.container{
	max-width: 120rem;
	width: 90%;
	margin: 0 auto;
}

.menu-toggle{
	position: fixed;
	top: 2.5rem;
	right: 2.5rem;
	color: #eeeeee;
	font-size: 3rem;
	cursor: pointer;
	z-index: 1000;
	display: none;
}

nav{
	padding-top: 5rem;

	display: flex;
	justify-content: space-between;
	align-items: center;
	text-transform: uppercase;
	font-size: 1.6rem;
}

.brand{
	font-size: 3rem;
	font-weight: 300;
	-webkit-transform: translateX(-100rem);
	        transform: translateX(-100rem);
	-webkit-animation: slideIn .5s forwards;
	        animation: slideIn .5s forwards;
}

.brand span{
	color: crimson;
}

nav ul{
	display: flex;
}

nav ul li{
	list-style: none;
	-webkit-transform: translateX(100rem);
	        transform: translateX(100rem);
	-webkit-animation: slideIn .5s forwards;
	        animation: slideIn .5s forwards;
}

nav ul li:nth-child(1){
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
}

nav ul li:nth-child(2){
	-webkit-animation-delay: .5s;
	        animation-delay: .5s;
}

nav ul li:nth-child(3){
	-webkit-animation-delay: 1s;
	        animation-delay: 1s;
}

nav ul li:nth-child(4){
	-webkit-animation-delay: 1.5s;
	        animation-delay: 1.5s;
}

nav ul li a{
	padding: 1rem 0;
	margin: 0 3rem;
	position: relative;
	letter-spacing: 2px;
}

nav ul li a:last-child{
	margin-right: 0;
}

nav ul li a::before,
nav ul li a::after{
	content: '';
	position: absolute;
	width: 100%;
	height: 2px;
	background-color: crimson;
	left: 0;
	-webkit-transform: scaleX(0);
	        transform: scaleX(0);
	-webkit-transition: all .5s;
	transition: all .5s;
}

nav ul li a::before{
	top: 0;
	-webkit-transform-origin: left;
	        transform-origin: left;
}

nav ul li a::after{
	bottom: 0;
	-webkit-transform-origin: right;
	        transform-origin: right;
}

.overlay{
	background-color: rgba(0,0,0,.95);
	position: fixed;
	right: 0;
	left: 0;
	top: 0;
	bottom: 0;
	-webkit-transition: opacity 650ms;
	transition: opacity 650ms;
	-webkit-transform: scale(0);
	        transform: scale(0);
	opacity: 0;
  display: none;
}

nav ul li a:hover::before,
nav ul li a:hover::after{
	-webkit-transform: scaleX(1);
	        transform: scaleX(1);
}

@-webkit-keyframes slideIn {
	from{

	}
	to{
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@keyframes slideIn {
	from{

	}
	to{
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@media screen and (max-width: 700px){

	.menu-toggle{
		display: block;
	}

	nav{
		padding-top: 0;
		display: none;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: center;
		height: 100vh;
		text-align: center;
	}

	nav ul{
		flex-direction: column;
	}

	nav ul li{
		margin-top: 5rem;
	}

	nav ul li a{
		margin: 0;
		font-size: 2.5rem;
	}

	.brand{
		font-size: 5rem;
	}

  .overlay.menu-open,
  nav.menu-open{
	  display: flex;
	  -webkit-transform: scale(1);
	          transform: scale(1);
	  opacity: 1;
  }

}

.hidelog{
  display: none
}
.hide{
  display: none
}

.button_no_style{
  border: none;
  padding: 0;
  background: none;
  color: #007bff;
}


